import React from "react";

const HeartSVG = (props) => {
  const { likedNum } = props;
  return likedNum ? (
    <div className="row ml-2">
      <svg
        width="1em"
        height="1em"
        viewBox="0 0 16 16"
        className="mt-1 mr-1 bi bi-heart-fill text-danger"
        fill="currentColor"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          fillRule="evenodd"
          d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z"
        />
      </svg>
      <p className="mb-0 ">{likedNum}人觉得很赞</p>
    </div>
  ) : null;
};

