const TalkSVG = (props) => {
  const { a, onTalk } = props;
  return (
    <svg
      width="100%"
      viewBox="0 0 16 16"
      className={
        a.talk
          ? "bi bi-chat-left-dots-fill pointer mr-1 text-primary"
          : "bi bi-chat-left-dots-fill pointer mr-1"
      }
      fill="currentColor"
      xmlns="http://www.w3.org/2000/svg"
      fillRule="evenodd"
      onClick={onTalk}
    >
      <path d="M14 1H2a1 1 0 0 0-1 1v11.586l2-2A2 2 0 0 1 4.414 11H14a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1zM2 0a2 2 0 0 0-2 2v12.793a.5.5 0 0 0 .854.353l2.853-2.853A1 1 0 0 1 4.414 12H14a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z" />
      <path d="M5 6a1 1 0 1 1-2 0 1 1 0 0 1 2 0zm4 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0zm4 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
    </svg>
  );
};

const ShareSVG = (props) => {
  const { a, onShare } = props;
  return (
    <svg
      width="100%"
      viewBox="0 0 16 16"
      className={
        a.share
          ? "bi bi-arrow-right-circle pointer text-primary"
          : "bi bi-arrow-right-circle pointer"
      }
      fill="currentColor"
      xmlns="http://www.w3.org/2000/svg"
      onClick={onShare}
    >
      <path
        fillRule="evenodd"
        d="M8 15A7 7 0 1 0 8 1a7 7 0 0 0 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"
      />
      <path
        fillRule="evenodd"
        d="M4 8a.5.5 0 0 0 .5.5h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5H4.5A.5.5 0 0 0 4 8z"
      />
    </svg>
  );
};
const PlusSVG = (props) => {
  const { onNewAticle } = props;
  return (
    <svg
      width="2em"
      height="2em"
      viewBox="0 0 16 16"
      className={
        props.newAticle
          ? "bi bi-bookmark-plus pointer text-primary"
          : "bi bi-bookmark-plus pointer"
      }
      fill="currentColor"
      xmlns="http://www.w3.org/2000/svg"
      onClick={onNewAticle}
    >
      <path
        fillRule="evenodd"
        d="M2 2a2 2 0 0 1 2-2h8a2 2 0 0 1 2 2v13.5a.5.5 0 0 1-.777.416L8 13.101l-5.223 2.815A.5.5 0 0 1 2 15.5V2zm2-1a1 1 0 0 0-1 1v12.566l4.723-2.482a.5.5 0 0 1 .554 0L13 14.566V2a1 1 0 0 0-1-1H4z"
      />
      <path
        fillRule="evenodd"
        d="M8 4a.5.5 0 0 1 .5.5V6H10a.5.5 0 0 1 0 1H8.5v1.5a.5.5 0 0 1-1 0V7H6a.5.5 0 0 1 0-1h1.5V4.5A.5.5 0 0 1 8 4z"
      />
    </svg>
  );
};

export { LikeSVG, TalkSVG, ShareSVG, HeartSVG, PlusSVG };
